<template>
	<div>
		<div class="item-detail-show">
			<div class="item-detail-left">
				<div class="item-detail-big-img">
					<img :src="goodsInfo.ImgList[imgIndex].Src" alt="">
				</div>
				<div class="item-detail-img-row">
					<div class="item-detail-img-small" v-for="(item, index) in goodsInfo.ImgList" :key="index" @mouseover="showBigImg(index)">
						<img :src="item.Src" alt="">
					</div>
				</div>
			</div>
			<div class="item-detail-right">
				<div class="item-detail-title">
					<p>{{goodsInfo.GoodsName}}</p>
				</div>
				<div class="item-detail-price-row">
					<div class="item-price-left">
						<div class="item-price-row">
							<p>
								<span class="item-price-title">单价</span>
								<span class="item-price">￥{{goodsInfo.SpecialOffer.toFixed(2)}}</span>
							</p>
						</div>
					</div>
				</div>
				<div class="item-select">
					<p>
						<span class="item-price-title">建议零售价</span>
						<span class="item-price-title">￥{{goodsInfo.MSRP.toFixed(2)}}</span>
					</p>
				</div>
				<div class="item-select">
					<p>
						<span class="item-price-title">条码</span>
						<span class="item-price-title">{{serialString}}</span>
					</p>
				</div>
				<div class="item-select">
					<p>
						<span class="item-price-title">规格</span>
						<span class="item-price-title">{{goodsInfo.Specifications}}</span>
					</p>
				</div>
				<div class="item-select">
					<p>
						<span class="item-price-title">保质期</span>
						<span class="item-price-title">{{goodsInfo.ShelfLife}} 月</span>
					</p>
				</div>
				<div class="item-select">
					<p>
						<span class="item-price-title">装箱数</span>
						<span class="item-price-title">{{goodsInfo.PackingNumber}}</span>
					</p>
				</div>
				<div class="item-select">
					<p>
						<span class="item-price-title">整件价</span>
						<span class="item-price-title">￥{{goodsInfo.singlePrice}}</span>
					</p>
				</div>
				<div class="item-select" v-for="(items, index) in goodsInfo.ValuesList" :key="index">
					<div class="item-select-title">
						<p>{{specs[goodsInfo.ValuesList[index][0].CategoryType - 1]}}</p>
					</div>
					<div class="item-select-column">
						<div class="item-select-row">
							<RadioGroup v-model="selectString[index]" type="button" @on-change="select(index)">
								<Radio v-for="(item, index1) in items" :key="index1" :label="item.GoodsValueName"></Radio>
							</RadioGroup>
						</div>
					</div>
				</div>
				<br>
				<div class="add-buy-car-box">
					<div class="add-buy-car">
						<InputNumber :min="1" :max="maxCount" v-model="count" size="large"></InputNumber>
						<Button type="error" size="large" @click="addShoppingCartBtn" :disabled="goodsInfo.Stock <= 0">加入购物车</Button>
						<span v-if="goodsInfo.Quota !== -1" style="margin-left: 15px; font-size: 14px;">限购：{{goodsInfo.Quota}}件</span>
						<span style="margin-left: 15px; font-size: 14px;">库存：{{goodsInfo.Stock}}件</span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapState, mapActions} from 'vuex';

	export default {
		name: 'ShowGoods',

		data() {
			return {
				count: 1,
				maxCount: 10000,
				imgIndex: 0,
				selectString: [],
				selectIndex: [],
				specs: ['味道', '颜色', '尺寸'],
				serialNumbers: [],
				serialString: '',
			};
		},
		
		async created() {
			await this.loadGoodsInfo(this.$route.query.id);
			this.initSelectString();

			this.maxCount = this.goodsInfo.Quota > 0 ? this.goodsInfo.Quota : this.maxCount;
			this.maxCount = Math.min(this.maxCount, this.goodsInfo.Stock);
		},

		computed: {
			...mapState(['goodsInfo', 'shoppingCart']),
		},

		methods: {
			...mapActions(['loadGoodsInfo', 'loadShoppingCart']),

			initSelectString() {
				for (let i = 0; i < this.goodsInfo.ValuesList.length; i++) {
					this.selectString.push(this.goodsInfo.ValuesList[i][0].GoodsValueName);
					this.serialNumbers.push(this.goodsInfo.ValuesList[i][0].SerialNumber);
					this.selectIndex[i] = this.goodsInfo.ValuesList[i][0].GoodsValueID;
				}
				this.serialString = this.serialNumbers.join('-');
			},

			select(index) {
				this.goodsInfo.ValuesList[index].forEach(item => {
					if (item.GoodsValueName === this.selectString[index]) {
						this.selectIndex[index] = item.GoodsValueID;
						this.serialNumbers[index] = item.SerialNumber;
					}
				});
				this.serialString = this.serialNumbers.join('-');
			},

			showBigImg(index) {
				this.imgIndex = index;
			},

			async addShoppingCartBtn() {
				await this.loadShoppingCart();
				let specsString = this.selectString.join('-');
				let inCart = false;
				for (let i = 0; i < this.shoppingCart.length; i++) {
					if (parseInt(this.shoppingCart[i].GoodsID) === parseInt(this.goodsInfo.goodsID) && this.shoppingCart[i].specs === specsString) {
						inCart = true;
						break;
					}
				}
				if (inCart) {
					this.$Modal.confirm({
						title: "提示",
						content: "该商品已在购物车中，是否继续添加？",
						onOk: async () => {
							await this.addToCart();
						},
						onCancel: () => {}
					});
				} else {
					await this.addToCart();
				}
			},

			async addToCart() {
				let specs = [];
				for (let i = 0; i < this.selectIndex.length; i++) {
					let tmp = {
						'CategoryType': i + 1,
						'GoodsValueID': this.selectIndex[i],
					};
					specs.push(tmp);
				}
				let res = await this.$request.addCart(this.goodsInfo.goodsID, this.count, specs);
				if (res) {
					this.$Message.success('加入购物车成功');
					// this.$router.push('/home/MyshoppingCart');
				}
			},
		},
	};
</script>

<style scoped>
	/******************商品图片及购买详情开始******************/
	.item-detail-show {
		width: 60%;
		margin: 15px auto;
		display: flex;
		flex-direction: row;
		background-color: #fff;
	}

	.item-detail-left {
		width: 350px;
		margin-right: 30px;
	}

	.item-detail-big-img {
		width: 350px;
		height: 350px;
		box-shadow: 0px 0px 8px #ccc;
		cursor: pointer;
	}

	.item-detail-big-img img {
		width: 100%;
		height: 100%;
	}

	.item-detail-img-row {
		margin-top: 15px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

	.item-detail-img-small {
		width: 68px;
		height: 68px;
		box-shadow: 0px 0px 8px #ccc;
		cursor: pointer;
	}

	.item-detail-img-small img {
		width: 100%;
		height: 100%;
	}

	/*商品选购详情*/
	.item-detail-right {
		width: 600px;
		display: flex;
		flex-direction: column;
	}

	.item-detail-title p {
		color: #666;
		font-size: 20px;
	}

	.item-detail-express {
		font-size: 14px;
		padding: 2px 3px;
		border-radius: 3px;
		background-color: #e4393c;
		color: #fff;
	}

	/*商品标签*/
	.item-detail-tag {
		font-size: 12px;
		color: #e4393c;
	}

	/*价格详情等*/
	.item-detail-price-row {
		padding: 5px;
		margin-top: 15px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		background-color: #f3f3f3;
	}

	.item-price-left {
		display: flex;
		flex-direction: column;
	}

	.item-price-title {
		color: #999999;
		font-size: 14px;
		margin-right: 15px;
	}

	.item-price-row {
		margin: 5px 0px;
	}

	.item-price {
		color: #e4393c;
		font-size: 23px;
		cursor: pointer;
	}

	.item-price-full-cut {
		margin-right: 5px;
		padding: 3px;
		color: #e4393c;
		font-size: 12px;
		background-color: #ffdedf;
		border: 1px dotted #e4393c;
		cursor: pointer;
	}

	.item-remarks-sum {
		padding-left: 8px;
		border-left: 1px solid #ccc;
	}

	.item-remarks-sum p {
		color: #999999;
		font-size: 12px;
		line-height: 10px;
		text-align: center;
	}

	.item-remarks-num {
		line-height: 18px;
		color: #005eb7;
	}

	.item-select {
		display: flex;
		flex-direction: row;
		margin-top: 15px;
	}

	.item-select-title {
		color: #999999;
		font-size: 14px;
		margin-right: 15px;
	}

	.item-select-column {
		display: flex;
		flex-direction: column;
	}

	.item-select-row {
		display: flex;
		flex-direction: row;
		margin-bottom: 8px;
	}

	.item-select-box {
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.item-select-img {
		width: 36px;
	}

	.item-select-box {
		padding: 5px;
		margin-right: 8px;
		background-color: #f7f7f7;
		border: 0.5px solid #ccc;
		cursor: pointer;
	}

	.item-select-box:hover {
		border: 0.5px solid #e3393c;
	}

	.item-select-box-active {
		border: 0.5px solid #e3393c;
	}

	.item-select-img img {
		width: 100%;
	}

	.item-select-intro p {
		margin: 0px;
		padding: 5px;
	}

	.item-select-class {
		padding: 5px;
		margin-right: 8px;
		background-color: #f7f7f7;
		border: 0.5px solid #ccc;
		cursor: pointer;
	}

	.item-select-class:hover {
		border: 0.5px solid #e3393c;
	}

	.add-buy-car-box {
		width: 100%;
		margin-top: 15px;
		border-top: 1px dotted #ccc;
	}

	.add-buy-car {
		margin-top: 15px;
	}

	/******************商品图片及购买详情结束******************/
</style>



