<template>
	<div>
		<div class="item-intro-show">
			<div class="item-intro-detail" ref="itemIntroDetail">
				<div class="item-intro-nav item-tabs">
					<Tabs>
						<TabPane label="商品介绍">
							<!--<div class="remarks-title">-->
								<!--<span>商品介绍</span>-->
							<!--</div>-->
							<div class="item-param-container">
								<span class="item-param-content"
								      style="font-size: 14px; padding-left: 30px; padding: 5px;">{{goodsInfo.Describe}}</span>
							</div>
							<div class="item-intro-img" ref="itemIntroGoods">
								<img :src="item.Src" v-for="(item,index) in goodsInfo.ImgList" :key="index">
							</div>
						</TabPane>
						<!--<TabPane label="规格参数">-->
							<!--<div class="remarks-title">-->
								<!--<span>规格参数</span>-->
							<!--</div>-->
							<!--<div class="item-param-container">-->
								<!--<span class="item-param-box">-->
				                  <!--<span class="item-param-title">商品名称: </span>-->
				                  <!--<span class="item-param-content">{{goodsInfo.GoodsName}}</span>-->
				                <!--</span>-->
								<!--<span class="item-param-box">-->
				                  <!--<span class="item-param-title">商品编号: </span>-->
				                  <!--<span class="item-param-content">{{goodsInfo.SerialNumber}}</span>-->
				                <!--</span>-->
							<!--</div>-->
						<!--</TabPane>-->
						<!--<TabPane label="售后保障">-->
							<!--<ShowProductWarranty></ShowProductWarranty>-->
						<!--</TabPane>-->
					</Tabs>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import ShowProductWarranty from './ShowProductWarranty';
	import {mapState} from 'vuex';

	export default {
		name: 'ShowGoodsDetail',

		data() {
			return {
				tagsColor: ['blue', 'green', 'red', 'yellow']
			};
		},

		components: {
			ShowProductWarranty
		},

		computed: {
			...mapState(['goodsInfo'])
		},

		methods: {
			changeHeight() {
				let heightCss = window.getComputedStyle(this.$refs.itemIntroGoods).height;
				heightCss = parseInt(heightCss.substr(0, heightCss.length - 2)) + 89;
				this.$refs.itemIntroDetail.style.height = heightCss + 'px';
			}
		},

		updated() {
			this.$nextTick(() => {
				setTimeout(this.changeHeight, 100);
				setTimeout(this.changeHeight, 1000);
				setTimeout(this.changeHeight, 3000);
				setTimeout(this.changeHeight, 5000);
				setTimeout(this.changeHeight, 10000);
				setTimeout(this.changeHeight, 15000);
				setTimeout(this.changeHeight, 20000);
				setTimeout(this.changeHeight, 25000);
				setTimeout(this.changeHeight, 30000);
				setTimeout(this.changeHeight, 50000);
			});
		},
	};
</script>

<style scoped>
	/***************商品详情介绍和推荐侧边栏开始***************/
	.item-intro-show {
		width: 60%;
		margin: 15px auto;
		display: flex;
		flex-direction: row;
		background-color: #fff;
	}

	.item-recommend-img img {
		width: 100%;
	}

	.item-intro-detail {
		margin-left: 0px;
		height: 600px;
		width: 100%;
	}

	.item-intro-nav {
		width: 100%;
		height: 38px;
		background-color: #F7F7F7;
		border-bottom: 1px solid #E4393C;
	}

	.item-intro-nav ul {
		margin: 0px;
		padding: 0px;
		list-style: none;
	}

	.item-intro-nav li {
		float: left;
		height: 100%;
		width: 120px;
		line-height: 38px;
		text-align: center;
		color: #E4393C;
	}

	.item-intro-nav li:first-child {
		background-color: #E4393C;
		color: #fff;
	}

	.item-intro-img {
		width: 100%;
	}

	.item-intro-img img {
		width: 100%;
	}

	/************* 商品参数 *************/
	.item-param-container {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;
	}

	.item-param-box {
		padding: 5px;
		padding-left: 30px;
		width: 350px;
		height: 36px;
		font-size: 14px;
		/* text-align: center; */
		/* background-color: #ccc; */
	}

	.item-param-title {
		color: #232323;
	}

	.item-param-content {
		color: #999;
	}

	.remarks-title {
		padding-left: 15px;
		height: 36px;
		font-size: 16px;
		font-weight: bolder;
		line-height: 36px;
		color: #666666;
		background-color: #F7F7F7;
	}

	.remarks-bar span {
		margin-right: 15px;
	}

	/***************商品详情介绍和推荐侧边栏结束***************/
</style>

<style>
	.item-tabs > .ivu-tabs > .ivu-tabs-bar .ivu-tabs-tab {
		border-radius: 0px;
		color: #999;
		height: 38px;
		background: #F7F7F7;
	}

	.item-tabs > .ivu-tabs > .ivu-tabs-bar .ivu-tabs-tab-active {
		color: #fff;
		background-color: #E4393C;
	}

	.item-tabs > .ivu-tabs > .ivu-tabs-bar .ivu-tabs-tab-active:before {
		content: '';
		display: block;
		width: 100%;
		height: 1px;
		color: #fff;
		background: #F7F7F7;
		position: absolute;
		top: 0;
		left: 0;
	}

	.ivu-rate-star-full:before, .ivu-rate-star-half .ivu-rate-star-content:before {
		color: #E4393C;
	}
</style>



