<template>
	<div>
		<Search></Search>
		<div class="shop-item-path">
			<div class="shop-nav-container">
				<Breadcrumb>
					<BreadcrumbItem to="/">首页</BreadcrumbItem>
					<BreadcrumbItem>{{level_1}}</BreadcrumbItem>
					<BreadcrumbItem v-if="level_2">{{level_2}}</BreadcrumbItem>
				</Breadcrumb>
			</div>
		</div>
		<!-- 商品信息展示 -->
		<ShowGoods></ShowGoods>
		<!-- 商品详细展示 -->
		<ShowGoodsDetail></ShowGoodsDetail>
		<Footer></Footer>
	</div>
</template>

<script>
	import Search from './Search';
	import Footer from './footer/Footer';
	import ShowGoods from './goodsDetail/ShowGoods';
	import ShowGoodsDetail from './goodsDetail/ShowGoodsDetail';
	import {mapActions, mapState} from 'vuex';

	export default {
		name: 'GoodsDetail',

		components: {
			Search,
			ShowGoods,
			ShowGoodsDetail,
			Footer
		},

		data() {
			return {
				level_1: '',
				level_2: '',
			};
		},

		async created() {
			await this.loadGoodsInfo(this.$route.query.id);
			this.initCategory();
		},

		beforeRouteEnter(to, from, next) {
			window.scrollTo(0, 0);
			next();
		},

		computed: {
			...mapState(['goodsInfo', 'goodsGroupOrigin'])
		},

		methods: {
			...mapActions(['loadGoodsInfo']),

			initCategory() {
				let id = this.goodsInfo.GoodsGroups_ID;
				for (let i = 0; i < this.goodsGroupOrigin.length; i++) {
					if (this.goodsGroupOrigin[i].ID === id) {
						if (this.goodsGroupOrigin[i].ParentID === 0) {
							this.level_1 = this.goodsGroupOrigin[i].Name;
							id = null;
						} else {
							this.level_2 = this.goodsGroupOrigin[i].Name;
							id = this.goodsGroupOrigin[i].ParentID;
						}
						break;
					}
				}
				if (id) {
					for (let i = 0; i < this.goodsGroupOrigin.length; i++) {
						if (this.goodsGroupOrigin[i].ID === id) {
							this.level_1 = this.goodsGroupOrigin[i].Name;
							break;
						}
					}
				}
			},
		},
	};
</script>

<style scoped>
	.shop-item-path {
		height: 38px;
		background-color: rgb(236, 235, 235);
		line-height: 38px;
		color: #2c2c2c;
	}

	.shop-nav-container {
		margin: 0px auto;
		width: 60%;
	}
</style>



